import React, { Component } from 'react';
import { connect } from "react-redux";
import SVG from 'react-inlinesvg';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import moment from 'moment';
import { Link } from 'react-router-dom';

class BillingOverview extends Component {
    state = {
        expiresAt: null,
        plan: "UGC Suite"
    }
    componentDidMount() {
        const { userData } = this.props
        const { expiresAt } = this.state

        if (userData && userData.user && userData.UserPlan && Object.keys(userData.UserPlan).length > 0 && expiresAt == null) {
            const myMoment = moment(new Date(userData.UserPlan.expiresAt * 1000))
            this.setState({ expiresAt: (myMoment.diff(moment(), 'days')), plan: `UGC Suite ${userData.UserPlan.Plan.name}` })
        }
    }
    render() {
        const { expiresAt, plan } = this.state

        return <div className="container container-xs">
            <div className="mt-5">
                <h3 className="fs-6 text-gray-800">Overview</h3>
            </div>
            <div className="card mb-xxl-4 mb-3">
                <div className="card-body px-3 py-3 px-xxl-4 py-xxl-5 px-xl-3 py-xl-4">
                    <div className="row">
                        <div className="col-lg-3 col-sm-6">
                            <div className="mb-lg-0 mb-3">
                                <p className="mb-lg-4 mb-2 text-gray-600 fs-7">Current Plan</p>
                                <h6 className="text-gray-800">{plan}</h6>
                            </div>
                        </div>
                        <div className="col-lg-3 col-sm-6">
                            <div className="mb-lg-0 mb-3">
                                <p className="mb-lg-4 mb-2 text-gray-600 fs-7">Status</p>
                                <h6 className="text-gray-800">{expiresAt && expiresAt > 0 ? `Active` : `In Active`}</h6>
                            </div>
                        </div>
                        <div className="col-lg-3 col-sm-6">
                            <div className="mb-lg-0 mb-3">
                                <p className="mb-lg-4 mb-2 text-gray-600 fs-7">Days left on trial</p>
                                <h6 className="text-gray-800">{expiresAt ? expiresAt : 0} Days</h6>
                            </div>
                        </div>
                        <div className="col-lg-3 col-sm-6">
                            <div className="text-lg-center mt-2 mt-lg-3">
                                <Link to={'price'} className="btn btn-primary btn-lg">
                                    <span className="svg-icon">
                                        <SVG src={baseUrl('media/icons/svg-icons/star-shine.svg')} height={20} width={20} />
                                    </span>
                                    Upgrade now
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    }
}

const mapStateToProps = (state) => {
    return {
        userData: state.authenticated.userData
    }
}

export default connect(mapStateToProps)(BillingOverview);